.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  height: 90vh;
  width: 100vw;

  background-color: black;
  color: white;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100%;

  font-family: 'Roboto';
}

.subtitle {
  margin-top: 10px;
  font-weight: 300;
}

.image {
  height: 400px;
}

.cabou {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.denovo {
  text-decoration: underline;
}
